.Main_L {
  display: inline-block;
  width: 20%;
  font-size: 2rem;
  font-weight: 700;
  padding: 2rem;
  border: 2px;
  border-radius: 5px;
  -webkit-box-shadow: 0 6px 12px gray;
          box-shadow: 0 6px 12px gray;
  text-decoration: none;
  text-align: center;
  font-style: italic;
}

.Main_L:hover {
  background: -webkit-gradient(linear, left top, left bottom, from(rgb(219, 236, 241)), to(rgb(64, 162, 207)));
  background: -webkit-linear-gradient(rgb(219, 236, 241), rgb(64, 162, 207));
  background: -o-linear-gradient(rgb(219, 236, 241), rgb(64, 162, 207));
  background: linear-gradient(rgb(219, 236, 241), rgb(64, 162, 207));
  -webkit-transform: translateY(0.4rem);
      -ms-transform: translateY(0.4rem);
          transform: translateY(0.4rem);
}



