.App {
  text-align: center;
  min-height: 100vh;
  background: -webkit-gradient(linear, left top, right top, from(rgb(71, 255, 240)), to(rgb(100, 207, 255)));
  background: -webkit-linear-gradient(left, rgb(71, 255, 240), rgb(100, 207, 255));
  background: -o-linear-gradient(left, rgb(71, 255, 240), rgb(100, 207, 255));
  background: linear-gradient(to right, rgb(71, 255, 240), rgb(100, 207, 255));
}
.World {
    font-family: 'Monoton', cursive;
    font-size: 3rem;
    color: rgb(9, 97, 109);
    padding: 3.5rem;
    -webkit-margin-before: 0;
            margin-block-start: 0;
}






